import { gql } from "@apollo/client";

export default gql`
  query getEmployees($isRTL: Boolean, $resTypes: [Int]) {
    getEmployees(isRTL: $isRTL, resTypes: $resTypes) {
      ok
      error
      data {
        _id
        branch
        autoNo
        docNo
        name
        nameAr
        color
        resType

        amount
        totalinvoiced
        totalpaid
        toatlExpenses

        progress
        evQty
        evDone

        phone
        email
        comPercent
        departmentId
        departmentName
        departmentNameAr
        departmentColor
        daysoff
        info
        userId

        documentNo
        startDate
        endDate
        cost
        model
        type

        createdAt
        updatedAt
      }
    }
  }
`;
